.menu {
    display: flex;

    .menu-item {
        font-size: 14px;
        line-height: 16px;
        padding: 16px;
        display: flex;
        align-items: center;
        color: #121212;
        opacity: 0.8;

        &:last-child {
            padding-right: 0;
        }
        &:hover {
            color: #9c9c9c;
        }
        &:active {
            color: #121212;
            opacity: 1;
        }
    }
}

